import React from "react";
import * as THREE from "three";
import faceFilter from "facefilter";
import JeelizResizer from "./JeelizResizer";
import JeelizThreejsHelper from "./JeelizThreejsHelper";
import { Redirect } from "react-router";
import {
  DotScreenEffect,
  EffectComposer,
  HueSaturationEffect,
  EffectPass,
  RenderPass,
  BrightnessContrastEffect
} from "postprocessing";
import "./App.css";

class Camera extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      redirect: null,
      currentPhoto: null
    };

    this.detect_callback = this.detect_callback.bind(this);
    this.onInitialResizeReady = this.onInitialResizeReady.bind(this);
    this.onFaceFilterInitReady = this.onFaceFilterInitReady.bind(this);
    this.initThreeScene = this.initThreeScene.bind(this);
    this.onTrack = this.onTrack.bind(this);
    this.componentDidMount = this.componentDidMount.bind(this);
    this.onShutter = this.onShutter.bind(this);
    this.render = this.render.bind(this);
  }

  detect_callback(faceIndex, isDetected) {
    if (isDetected) {
      console.log("INFO in detect_callback() : DETECTED");
    } else {
      console.log("INFO in detect_callback() : LOST");
    }
  }

  initThreeScene(spec) {
    const threeStuffs = JeelizThreejsHelper.init(spec, this.detect_callback);
    const scene = threeStuffs.scene;
    const renderer = threeStuffs.renderer;

    this.ctx = spec.GL;

    // CREATE A CUBE
    const cubeGeometry = new THREE.BoxGeometry(1, 1, 1);

    var loader = new THREE.TextureLoader();
    loader.setPath("/");

    var texture = loader.load("./icon512.png");
    texture.wrapS = THREE.RepeatWrapping;
    texture.wrapT = THREE.RepeatWrapping;

    var material = new THREE.MeshBasicMaterial({
      map: texture
    });

    //const cubeMaterial = new THREE.MeshNormalMaterial();
    const threeCube = new THREE.Mesh(cubeGeometry, material);
    threeCube.frustumCulled = false;
    //threeStuffs.faceObject.add(threeCube);

    //CREATE THE CAMERA
    var aspecRatio = spec.canvasElement.width / spec.canvasElement.height;
    this.camera = new THREE.PerspectiveCamera(20, aspecRatio, 0.1, 100);

    // Prepare postprocessing
    this.composer = new EffectComposer(renderer);
    this.composer.addPass(new RenderPass(scene, this.camera));

    // Create effects
    const saturationEffect = new HueSaturationEffect({ saturation: -1 });
    const brightnessContrastEffect = new BrightnessContrastEffect({
      contrast: 0.5
    });
    const dotScreenEffect = new DotScreenEffect({ scale: 2 });
    // const colorDepthEffect = new ColorDepthEffect(BlendFunction.NORMAL, 2);
    // colorDepthEffect.setBitDepth(1);

    // Add the effects
    const effectPass = new EffectPass(
      this.camera,
      saturationEffect,
      brightnessContrastEffect,
      dotScreenEffect
    );
    effectPass.renderToScreen = true;
    this.composer.addPass(effectPass);
  }

  onInitialResizeReady(isError, bestVideoSettings) {
    //init_faceFilter(bestVideoSettings);
    console.log("best", bestVideoSettings);
    console.log("About to init");
    console.log(faceFilter);
    faceFilter.init({
      //you can also provide the canvas directly
      //using the canvas property instead of canvasId:
      canvasId: "jeeFaceFilterCanvas",
      NNCpath: "NNC.json", //path to JSON neural network model (NNC.json by default)
      callbackReady: this.onFaceFilterInitReady, //end callbackReady()

      //called at each render iteration (drawing loop)
      callbackTrack: this.onTrack
    }); //end init call
  }

  onFaceFilterInitReady(errCode, spec) {
    if (errCode) {
      console.log("AN ERROR HAPPENS. ERROR CODE =", errCode);
      return;
    }

    console.log("INFO: JEEFACEFILTERAPI IS READY");
    this.initThreeScene(spec);
  }

  onTrack(detectState) {
    JeelizThreejsHelper.render(detectState, this.camera, this.composer);
  }

  componentDidMount() {
    console.log(JeelizResizer);
    console.log("about to resize");
    JeelizResizer.size_canvas({
      canvasId: "jeeFaceFilterCanvas",
      callback: this.onInitialResizeReady
    });
  }

  onShutter() {
    // Store current canvas image somewhere, change the view to edit mode
    const canvas = document.getElementById("jeeFaceFilterCanvas");
    localStorage.setItem("photo", canvas.toDataURL());
    console.log("Setting photo to local");
    //this.setState({ redirect: "/edit" });
    window.location.hash = "/edit";
  }

  onClickFlip() {
    //faceFilter.update_video
    alert("Flip");

    //const videoElement = null;
    //faceFilter.update_videoElement(videoElement, () => {})
  }

  render() {
    const redirect = this.state.redirect;
    if (redirect) {
      return <Redirect to={redirect} />;
    }
    return (
      <div className="camera-view fill-screen">
        <canvas width="360" height="640" id="jeeFaceFilterCanvas" />
        <button
          className="avatar"
          style={{
            backgroundImage: `url(${this.props.userImageUrl})`,
            backgroundSize: "cover"
          }}
        />
        <button className="flip" onClick={this.onClickFlip}>
          <img src="/flip.svg" alt="Flip" />
        </button>
        <button className="shutter" onClick={this.onShutter} />
        <button className="stories">
          <img src="/stories.svg" alt="Stories" />
        </button>
      </div>
    );
  }
}

export default Camera;
