import React from "react";
import { Redirect } from "react-router";
import "./App.css";

class Edit extends React.Component {
  constructor(props) {
    super(props);

    let redirect = null;
    const initialData = localStorage.getItem("photo");
    console.log(initialData);
    if (!initialData) {
      alert("No image found");
      redirect = "/";
    }

    this.state = {
      redirect: redirect,
      imageData: initialData
    };

    this.imageDataLoaded = this.imageDataLoaded.bind(this);
    this.onClickCancel = this.onClickCancel.bind(this);
    this.onClickSend = this.onClickSend.bind(this);
  }

  componentDidMount() {
    // Load the image in memory first
    const img = new Image();
    img.onload = this.imageDataLoaded;
    img.src = this.state.imageData;
  }

  imageDataLoaded(e) {
    const img = e.target;
    const canvas = this.refs.canvas;

    // Resize canvas
    canvas.width = img.width;
    canvas.height = img.height;

    // Draw the image data onto the canvas
    const ctx = canvas.getContext("2d");
    ctx.drawImage(img, 0, 0);
    console.log("Image datas have been put!");
  }

  onClickCancel() {
    localStorage.removeItem("photo");
    this.setState({ redirect: "/" });
  }

  onClickSend() {
    // TMP: Send fax now
    fetch("https://stepheneisenhauer.com/misc/sfx.php", {
      method: "post",
      body: JSON.stringify({
        image: this.state.imageData
      })
    })
      .then(function(response) {
        console.log("Send fax 1");
      })
      .then(function(data) {
        console.log("Send fax 2");
      });

    this.setState({ redirect: "/send" });
  }

  render() {
    const redirect = this.state.redirect;
    if (redirect) {
      return <Redirect to={redirect} />;
    }
    return (
      <div className="edit-page fill-screen">
        <canvas ref="canvas" width="360" height="640" />
        <button className="cancel" onClick={this.onClickCancel}>
          <img src="/cancel.svg" alt="Cancel" />
        </button>
        <button className="send" onClick={this.onClickSend}>
          <img src="/send.svg" alt="Send" />
        </button>
        <div className="floating-label sendTo" onClick={this.onClickSend}>
          Send To
        </div>
      </div>
    );
  }
}

export default Edit;
