import React from "react";
import { Redirect } from "react-router";
import "./App.css";

class Send extends React.Component {
  constructor(props) {
    super(props);
    let redirect = null;
    this.state = {
      redirect: redirect
    };

    this.onClickCancel = this.onClickCancel.bind(this);
  }

  componentDidMount() {}

  onClickCancel() {
    this.setState({ redirect: "/edit" });
  }

  render() {
    const redirect = this.state.redirect;
    if (redirect) {
      return <Redirect to={redirect} />;
    }

    const friends = [
      { name: "Alice", photo: "", number: "12345" },
      { name: "Bob", photo: "", number: "12345" }
    ];

    const friendRows = friends.map(friend => <div>{friend.name}</div>);

    return (
      <div className="send-page fill-screen">
        <button className="cancel" onClick={this.onClickCancel}>
          <img src="/cancel.svg" alt="Cancel" />
        </button>
        <h2>Friends</h2>
        <ul>{friendRows}</ul>
      </div>
    );
  }
}

export default Send;
